<template>
  <div class="manage">
      <el-dialog
      :title="operateType==='add'? '新增用户':'更新用户'"
      :visible.sync ='isShow'
      >
      <common-form 
      :formlabel="operateFormlabel"
      :form="operateForm"
      :inline='true'
      ref="form"
      ></common-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="isShow = false">取消</el-button>
        <el-button type="primary" @click="confirm">确认</el-button>
      </div>
      </el-dialog>
      <div class="dialog-header">
        <el-button type="primary" @click="addUser">+新增</el-button>
        <common-form 
            :formlabel="formlabel"
            :form="searchForm"
            :inline="true"
            ref="form"
            style="padding-top:24px"
            >
           <el-button type="primary" @click="getList(searchForm.keyword)">搜索</el-button>
            </common-form>
        <!-- <el-button type="primary"></el-button> -->
        
      </div>
      <!-- 信息表格列表 -->
      <CommomTable
        :tableData="tableData"
        :tableLabel="tableLabel"
        :config="config"
        @changePage="getList"
        @edit="editUser"
        @del="delUser"
        >
        </CommomTable>
  </div>
</template>

<script>
import CommonForm from '@/components/commonForm2.vue'
import CommomTable from "@/components/CommonTable.vue"
import {getUser} from "@/api/data"
export default {
    name:'theUser',
    components:{CommonForm,CommomTable},
    data() {
      return {
        operateType:'add',
        isShow:false,
        operateFormlabel:[
                    {
                        model: 'name',
                        label: '姓名',
                        type: 'input'
                    },
                    {
                        model: 'age',
                        label: '年龄',
                        type: 'input'
                    },
                    {
                        model: 'sex',
                        label: '性别',
                        type: 'select',
                        opts: [
                            {
                                label: '男',
                                value: 1
                            },
                            {
                                label: '女',
                                value: 0
                            }
                        ]
                    },
                    {
                        model: 'birth',
                        label: '出生日期',
                        type: 'date'
                    },
                    {
                        model: 'addr',
                        label: '地址',
                        type: 'input'
                    }
            ],
        operateForm:{
          name:'',
          addr:'',
          age:'',
          birth:'',
          sex:''
        },
        formlabel: [{
          model:'keyword',
          label:'',
          type:'input'
        }],
        searchForm: {
          keyword:''
        },
        tableData: [],
            tableLabel: [
                {
                    prop: "name",
                    label: "姓名"
                },
                {
                    prop: "age",
                    label: "年龄"
                },
                {
                    prop: "sexLabel",
                    label: "性别"
                },
                {
                    prop: "birth",
                    label: "出生日期",
                    width: 200
                },
                {
                    prop: "addr",
                    label: "地址",
                    width: 320
                },
            ],
            config: {
                page: 1,
                total: 30,
                loading: ''
            }
      }
    },
    methods:{
        confirm(){
            if(this.operateType === 'edit'){
                this.$http.post('/user/edit',this.operateForm).then(res => {
                    console.log(res)
                    this.isShow = false
                    this.getList()
                })
            }else {
                this.$http.post('/user/add',this.operateForm).then(res => {
                    console.log(res)
                    this.isShow = false
                    this.getList()
                })
            }
        },
        addUser(){
            this.isShow = true
            this.operateType = 'add'
            this.operateForm = {
                name: '',
                addr: '',
                age: '',
                birth: '',
                sex: ''
            }
        },
        editUser(row){
            this.operateType = "edit"
            this.isShow = true
            this.operateForm = row
        },
        delUser(row){
            this.$confirm("此操作将永久删除该组件，是否继续？","提示",{ 
                confirmButtonText: "确认",
                cancelButtonText: "取消",
                type: "warning"
            }).then(() => {
                console.log(row);
                const id = row.id
                //delete请求删除需要的id的数据
                this.$http.delete("/user/del",{
                    params: { id }
                }).then(() => {
                    this.$message({
                        type: "success",
                        message: '删除成功'
                    })
                    this.getList()
                })
            }
            )
        },
        getList(name = ''){
            this.config.loading = true
            name ? (this.config.page = 1) : ''
            getUser({
                page: this.config.page,
                name
            }).then(({ data:res }) => {
                console.log(res,'res')
                this.tableData = res.list.map(item => { 
                    item.sexLabel = item.sex === 0 ? "女" : "男"
                    return item
                })
                this.config.total = res.count
                this.config.loading = false
            })
        }
    },
    mounted() {
      this.getList()
    }
}
</script>

<style scoped>
.dialog-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: -24px;
}
</style>